@import './colormap';

.header {

	border-bottom: 1px solid #ECECEC;

}

.header>.container {
	display: flex;
	align-items: center;
}

.header__nav {
	display: inline-flex;
	align-items: center;
	margin-right: auto;

	>.logo {
		padding: 24px 0 25px;
		margin-right: 93px;
	}
}

.header__menu {
	font-weight: 500;
	font-size: 14px;
	line-height: 1.14;
	letter-spacing: 0.02em;

	display: flex;


}

.header__menu-item {
	position: relative;
	// display: flex;
	// flex-direction: column;

	+.header__menu-item {
		margin-left: 50px;
	}

}

.body-index .active:first-child::after {
	content: '';
	height: 4px;
	width: 100%;
	position: absolute;
	bottom: -1px;
	left: 0;
	border-radius: 2px;
	background-color: getColor('secondary');
}

.body-portfolio .active:not(:first-child):after {
	content: '';
	height: 4px;
	width: 100%;
	position: absolute;
	bottom: -1px;
	left: 0;
	border-radius: 2px;
	background-color: getColor('secondary');
}




.header__menu-link {
	display: inline-flex;
	flex-direction: column;
	padding: 32px 0;
	transition: color $transitions;

	&:hover,
	&:focus {
		color: getColor('secondary');
	}
}




.header__contacts {


	@extend .header__menu;
	color: getColor('text');
}

.header__contacts-list {
	display: flex;
	align-items: center;
}

.header__contacts-item {
	// display: flex;
	// align-items: center;

	+.header__contacts-item {
		margin-left: 30px;
	}
}

.header__icon-envelope {
	margin-right: 10px;
	fill: currentColor;
}

.header__contacts-link {
	display: inline-flex;
	align-items: center;
	padding: 32px 0;
	transition: color $transitions;

	&:hover,
	&:focus {
		color: getColor('secondary');
	}
}